.theadCell {
  width: calc(100% + 16px);
  display: flex;
  align-content: center;
  justify-content: space-between;
}

.theadCellRow {
  display: flex;
  align-items: center;
}

.selected {
  display: flex;
  align-items: center;
  min-width: 75px;
  margin: 0 25px 0 0;
}

.product {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.col {
  display: flex;
  align-items: center;
}

.dateBox {
  display: flex;
  align-items: center;
}

.calendarIcon {
  font-size: 20px;
  color: #bfbfbf;
  margin: 0 15px 0 0;
}

.buttonBox {
  margin: 0 0 0 62px;
  min-width: 106px;
}

.posButton {
  min-width: 53px;
}

.posButtonIcon {
  font-size: 20px;
}

.imgBox {
  position: relative;
  width: 52px;
  height: 52px;
  overflow: hidden;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}

.img {
  object-fit: cover;
  position: absolute;
  width: auto;
  height: auto;
  max-width: calc(100% - 8px);
  max-height: calc(100% - 8px);
  min-width: calc(100% - 8px);
  min-height: calc(100% - 8px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.name {
  margin: 0 0 0 15px;
}

.theadButton {
  min-width: 53px;
}

.theadButtonIcon {
  font-size: 18px;
}

.form {
  flex: 1;
  padding: 0 0 0 50px;
}
